.shelfs h3 {
	text-align: center;
	margin: 0;
}
.shelfs {
	margin: 0;
	padding: 0;
}
.quote.right.icon {
	float: right;
}
.reds {
	color: red !important;
}
.quote.icon {
	margin: 0;
	padding: 0;
}
.shelf {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
.shelf-book {
	margin: 20px;
	width: 150px;
	height: 250px;
}
.shelf-book img {
	width: 150px;
	height: 200px;

	background-size: cover;
}
.book-settings {
	position: relative;
}
.circle-book-menu {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background: rgb(227, 61, 61);
	position: absolute;
	bottom: -5px;
	right: -15px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 8;
	cursor: pointer;
}
.circle-book-menu i {
	color: white;
}
